/**
* CSS file to hold styles that apply across the application
*/

@import url("https://fonts.googleapis.com/css2?family=Cormorant+Garamond:wght@300;400&family=Montserrat:wght@200;400;600;800&display=swap");

html,
body {
  font-family: "Playfair Display", sans-serif;
  height: 100%;
  margin: 0;
  overflow-x: hidden;
  padding: 0;
  width: 100%;
}

div#main {
  display: flex;
  flex-direction: column;
  width: 100vw;
}

section {
  position: relative;
  width: 100vw;
}

section.min-height {
  min-height: 100vh;
}

section.padding {
  padding: 3rem 0;
  color: "black";
}

section img.background {
  height: 100%;
  left: 50%;
  object-fit: cover;
  pointer-events: none;
  position: absolute;
  top: 0;
  transform: translate(-50%, 0);
  width: 100vmax;
  z-index: -1;
}

h1 {
  font-family: "Playfair Display", serif;
  font-size: 7rem;
  font-weight: 400;
  line-height: 0.8;
  margin: 0;
}

h2 {
  font-family: cursive;
  font-size: 3rem;
  font-weight: 300;
  margin: 0;
  padding-top: 1rem;
}

h3 {
  font-weight: 400;
  font-size: 1.25rem;
  margin: 0;
  
}

p {
  font-size: 18px;
  line-height: 1.5;
  margin-bottom: 0;
  font-weight: 300;
  color:white
}

p.large {
  font-size: 24px;
  color: white
}

p.small {
  font-size: 15px;
  color: white 
}

hr {
  margin: 2.5rem;
}

li {
  line-height: 2;
}

li::marker {
  color: #dfe855;
}

a {
  color: rgb(26, 68, 152);
  text-decoration: none;
}

a:hover {
  color: #d4ea48;
}

.container {
  display: grid;
  grid-gap: 10px;
  grid-template-columns: 330px 300px;
  margin: 20px auto;
  color:black
}

.container .box {
  border-radius: 10px;
  border: 1px solid rgb(24, 54, 172);
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  padding: 24px 16px;
  text-align: center;
}

img.socialIcon {
  height: 30px;
  width: 30px;
  color: #295bdb;
}
/* styles.css */

@keyframes slideIn {
  from {
    transform: translateY(-50%);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}


